<script>

export default {
    name: 'dt-page-contacts',
    props: {
        id: {
            type: String,
            default: 'x',
        },
    },
    data() {
        return {
            mapZoom: 9,
            mapCenter: {
                lat: 55.6847,
                lng: 37.3395
            },
        }
    },
}
</script>

<template lang="pug">

div.dt-page-contacts
    div.dt-page-contacts__header
        dkbr-figure.dt-page-contacts__image(
            src="static/pictures/dt/skoltech.jpg"
            alt="Skoltech")
        section.dt-page-contacts__title
            p
                span(v-if="isENG") Skolkovo Institute of Science and Technology
                span(v-if="isRUS") Сколковский институт науки и технологий
            p
                span(v-if="isENG") Laboratory Tensor networks and deep learning for applications in data mining
                span(v-if="isRUS") Лаборатория Тензорные сети и глубинное обучение для интеллектуального анализа данных
    div.dt-page-contacts__footer
        section.dt-page-contacts__address
            div.dt-page-contacts__address-block.fx-wide
                p.str
                    span(v-if="isENG") Skolkovo Innovation Center
                    span(v-if="isRUS") Территория Инновационного Центра “Сколково”
                p.str
                    span(v-if="isENG") Building 1 (3rd floor), Moscow, Russia, 143026
                    span(v-if="isRUS") улица Нобеля, д. 1 (3-ий этаж), Москва, Россия, 143026
                a.str(
                    :href=`isENG
                        ? "http://www.skoltech.ru/en/about/contacts/"
                        : "http://www.skoltech.ru/o-nas/kontakty/"`
                    target="_blank")
                    md-icon.md-accent(
                        style=`margin-right: 10px; text-decoration: underline;`) drive_eta
                    span(v-if="isENG") Learn more how to reach us
                    span(v-if="isRUS") Подробнее, как добраться
            div.dt-page-contacts__address-block.right
                p
                    span(v-if="isENG") Head of the Laboratory
                    span(v-if="isRUS") Руководитель Лаборатории
                p
                    span(v-if="isENG") Doctor, Prof. Andrzej Cichocki
                    span(v-if="isRUS") Доктор Анджей Чихоцкий
                p
                    md-icon.md-accent(style="margin-right: 10px;") email
                    span.em-spec A.Cichocki@skoltech.ru
            div.dt-page-contacts__address-block.right
                p
                    span(v-if="isENG") Campus operator
                    span(v-if="isRUS") Справочная Института
                p
                    md-icon.md-accent(style="margin-right: 10px;") phone
                    span.em-spec +7 (495) 280 14 81
        googlemaps-map.dt-page-contacts__map(
            :center.sync="mapCenter"
            :zoom.sync="mapZoom")
            googlemaps-marker(:position="mapCenter")
</template>

<style lang="sass">

@import '~@dt/sass/root.sass'

.dt-page-contacts
    &__header
        +flex(row, wrap, space-around, center)
    &__image
        flex: 1 0 300px
    &__title
        flex: 2 0 300px
        +padding(10px)
    &__footer
        +flex(row, wrap, space-between, center)
    &__address
        flex: 1 0 300px
        +padding(10px)
        +flex(row, wrap, space-between, flex-end)
        &-block
            +flex(column, nowrap, flex-end)
            +padding(10px)
    &__map
        flex: 2 0 300px
        max-width: 600px
        +width(300px)
        +height(400px)
</style>



